.screen {
	padding: 80px 0;
	display: flex;
	background-color: #f3f3f3;
}

.screen__wrap {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

.screen__item {
	width: 300px;
	height: 300px;
	background-size: cover;
	background-repeat: no-repeat;
	position: relative;
	flex-basis: 31.5%;
	margin-bottom: 30px;
}

.screen__overlay {
	display: flex;
	flex-direction: column;
	opacity: 0;
	justify-content: center;
	align-items: center;
	color: #fff;
	text-transform: uppercase;
	font-size: 16px;
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	background-color: rgba(0, 0, 0, .4);
	transition: .3s;

	&:hover{
		opacity: 1;
	}
		
}
	

.screen__link {
	display: block;
	font-size: 24px;
	margin-bottom: 20px;
}
	